<template>
  <li class="_Pending" v-if="user && owner && trip">
    <div class="flex">
      <img class="user-img-_Pending" :src="user.imgUrl" />
      <div>
        <h5 class="user-details-_Pending-name">{{user.firstName}} {{user.lastName}}</h5>
        <h5 class="user-details-_Pending-age">{{user.age}}</h5>
      </div>
    </div>
    <div class="flex">
      <i
        class="material-icons"
        v-if="loggedInUser && owner._id === loggedInUser._id"
        @click="append(user)"
      >check</i>
      <i
        class="material-icons"
        v-if="loggedInUser && owner._id === loggedInUser._id"
        @click="refuse(user)"
      >close</i>
    </div>
  </li>
</template>


<script>
export default {
  props: {
    owner: Object,
    user: Object,
    trip: Object
  },
  data() {
    return {};
  },
  computed: {
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    }
  },
  methods: {
    async append(user) {
      user.joining.push({ tripId: this.trip._id });
      const idx = user.pending.findIndex(
        currTrip => currTrip.tripId === this.trip._id
      );
      user.pending.splice(idx, 1);
      const idxOfTrip = this.owner.tripsOwner.findIndex(
        currTrip => currTrip.tripId === this.trip._id
      );
      const idxInWaitingList = this.owner.tripsOwner[
        idxOfTrip
      ].waitingList.findIndex(currUser => currUser.userId === user._id);
      this.owner.tripsOwner[idxOfTrip].waitingList.splice(idxInWaitingList, 1);
      this.owner.tripsOwner[idxOfTrip].participants.push({
        userId: this.user._id
      });
      try {
        await this.$store.dispatch({ type: "updateUser", user: user });
        await this.$store.dispatch({ type: "updateUser", user: this.owner });
        await this.$store.dispatch({
          type: "getUsers",
          tripId: { tripId: this.trip._id }
        });
      } catch (err) {
        console.log("not saved", err);
      }
    },
    async refuse(user) {
      const idx = user.pending.findIndex(trip => trip.tripId === this.trip._id);
      user.pending.splice(idx, 1);
      const idxOfTrip = this.owner.tripsOwner.findIndex(
        trip => trip.tripId === this.trip._id
      );
      const idxInWaitingList = this.owner.tripsOwner[
        idxOfTrip
      ].waitingList.findIndex(currUser => currUser.userId === user._id);
      this.owner.tripsOwner[idxOfTrip].waitingList.splice(idxInWaitingList, 1);
      try {
        await this.$store.dispatch({ type: "updateUser", user: user });
        await this.$store.dispatch({ type: "updateUser", user: this.owner });
        await this.$store.dispatch({
          type: "getUsers",
          tripId: { tripId: this.trip._id }
        });
      } catch (err) {
        console.log("not saved", err);
      }
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../styles/components/_Pending.scss";
</style>
