import AuthService from '../Services/AuthService.js'
import UserService from '../Services/UserService.js'
import SocketService from '../Services/SocketService.js'

export default {
    state: {
        users: [],
        loggedInUser: AuthService.getLoggedInUser(),
        waitingList: [],
        participantsList: [],
        loginModal: false
    },
    mutations: {
        setLoggedInUser(state, { user }) {
            state.loggedInUser = user;
        },
        setWaitingList(state, { waitingList }) {
            state.waitingList = waitingList;
        },
        setParticipantsList(state, { participantsList }) {
            state.participantsList = participantsList;
        },
        setUsers(state, { users }) {
            state.users = users;
        },
        updateUser(state, { user }) {
            const idx = state.users.findIndex(currUser => currUser._id === user._id)
            state.users.splice(idx, 1, user)
            if (state.loggedInUser && state.loggedInUser._id === user._id) state.loggedInUser = user;
        },
        toggleLogin(state) {
            state.loginModal = !state.loginModal;
        }
    },
    getters: {
        loggedInUser(state) {
            return state.loggedInUser;
        },
        waitingList(state) {
            return state.waitingList;
        },
        participantsList(state) {
            return state.participantsList;
        },
        loginModal(state) {
            return state.loginModal;
        }
    },
    actions: {
        async login(context, { user }) {
            try {
                const loggedInUser = await AuthService.login(user)
                context.commit({ type: 'setLoggedInUser', user: loggedInUser })
                SocketService.emit('create room', { loggedInUser })
            } catch (err) {
                throw err;
            }
        },
        async signup(context, { userCredential }) {
            try {
                const loggedInUser = await AuthService.signup(userCredential)
                context.commit({ type: 'setLoggedInUser', user: loggedInUser })
                SocketService.emit('createRoom', loggedInUser._id)
            } catch (err) {
                throw err;
            }
        },
        async logout(context) {
            await AuthService.logout()
            context.commit({ type: 'setLoggedInUser', user: null })
        },
        async getUserById(context, { id }) {
            try {
                return await UserService.getById(id)
            } catch (err) {
                throw err
            }
        },
        async updateUser(context, { user }) {
            try {
                const updatedUser = await UserService.save(user)
                context.commit({ type: 'updateUser', user: updatedUser })
                return updatedUser;
            } catch (err) {
                throw err;
            }
        },
        async getUsers(context, { tripId }) {
            const users = await UserService.query()
            context.commit({ type: 'setUsers', users })
            if (tripId) {
                const waitingList = users.filter(user => user.pending.find(id => (id.tripId === tripId.tripId)))
                context.commit({ type: 'setWaitingList', waitingList: waitingList })
                const participantsList = users.filter(user => user.joining.find(id => (id.tripId === tripId.tripId)))
                context.commit({ type: 'setParticipantsList', participantsList: participantsList })
                return participantsList;
            }
        }
    },
}