<template>
  <div class="trip-preview" @click="goDetails">
    <v-img xs2 :src="trip.imgUrl" aspect-ratio="1.6"></v-img>

    <div>
      <h4>{{trip.destination}} - {{trip.title}}</h4>
      <h5>{{trip.start}}-{{trip.end}}</h5>
    </div>
    <div class="avatars">
      <template v-if="participants.length">
        <v-avatar
          v-for="(participant, i) in participants"
          :key="i"
          class="participants"
          size="38px"
        >
          <img class="img-participant" v-if="i<3" :src="participant.imgUrl" />
        </v-avatar>
      </template>

      <!-- <v-avatar class="owner" size="60px">
        <img :src="owner.imgUrl" />
      </v-avatar> -->
    </div>
  </div>
</template>

<script>
export default {
  props: {
    trip: Object
    // validator:()=>{
    //     return
    // }
  },
  data() {
    return {
      participants: [],
      owner: {}
    };
  },
  async created() {
    try {
      this.participants = await this.$store.dispatch({
        type: "getUsers",
        tripId: { tripId: this.trip._id }
      });
      this.owner = await this.$store.dispatch({
        type: "getUserById",
        id: this.trip.owner._id
      });
    } catch (err) {
      console.log(err);
    }
  },
  computed: {},
  methods: {
    goDetails() {
      this.$router.push(`/TripDetails/${this.trip._id}`);
    }
  }
};
</script>



<style lang="scss" scoped>
@import "../styles/components/_TripPreview.scss";
</style>

